import React from "react";
import PropTypes from "prop-types";
import Head from "next/head";
import "styles/header.scss";
import "styles/prism-ghcolors.css";
function Header(props) {
  return (
    <Head>
      <title>{props.siteTitle}</title>
      <meta name="description" content={props.description} />
      <meta name="viewport" content="width=device-width, initial-scale=1" />
      {props.stylesheets &&
        props.stylesheets.length > 0 &&
        props.stylesheets.map((stylesheet, i) => {
          return <link key={i} rel="stylesheet" href={stylesheet} />;
        })}
      <style>{`
          body {
            font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
          }
      `}</style>
      {/* <link
        rel="stylesheet"
        href="https://cdnjs.cloudflare.com/ajax/libs/prism/1.15.0/themes/prism.min.css"
      /> */}
      {/* <link
        rel="stylesheet"
        href="https://cdnjs.cloudflare.com/ajax/libs/prism/1.15.0/themes/prism-okaidia.min.css"
      /> */}
      {/* <script
        src="https://cdnjs.cloudflare.com/ajax/libs/prism/1.15.0/prism.min.js"
        type="text/javascript"
      /> */}
      <link
        rel="icon"
        type="image/png"
        sizes="32x32"
        href="/static/image/favicon.png"
      />
      <script src="//twemoji.maxcdn.com/2/twemoji.min.js?11.2" />
    </Head>
  );
}

Header.propTypes = {
  siteTitle: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  stylesheets: PropTypes.array
};

export default Header;
